.skills {
    margin-top: 1300px;
    overflow: hidden;
    width: 100%;
    height: 300px;
    position: relative;
    padding-top: 20px;
}

.skills-container{
    position: relative;
}

.skill-item {
    position: absolute;
    margin-left: -120px;
}

.right-to-left{
    padding-top: 120px;
}

.skills h2{
    text-align: center;
    font-size: 40px;
    margin-top: -20px;
}

@media(max-width:1130px){
    .skills{
        margin-top: 1500px;
    }
}

@media(max-width:760px){
    .skills{
        margin-top: 2650px;
    }
}