header{
    width: 100vw;
    /* display: flex;
    justify-content: space-between;
    flex-direction: row; */
}

.header-left .fa-arrow-left{
    margin: 14px;
    border: 1px solid black; 
    width: 50px;
    height: 50px; 
    line-height: 50px;
    text-align: center;
    border-radius: 50%; 
}

.header-left .fa-arrow-left:hover{
    background-color: rgb(205, 92, 92);
}

.header-right{
    margin: 20px 20px;
}

nav{
    display: flex;
    flex-direction: row;
    justify-content: end;
}

nav a{
    text-decoration: none;
    color: black;
    padding: 10px;
    font-size: 25px;
}

nav a:hover{
    background-color: rgb(205, 92, 92);
}

.hidden{
    display: none;
}

@media(max-width: 400px){
    .header-right{
        margin: 0px;
    }
    nav{
        justify-content: left;
    }
    nav a{
        padding: 10px 6px;
    }
}